import axios from 'axios'

export default {
  state: {},
  mutations: {
    setAccountToken (state, token) {
      localStorage.setItem('accountToken', token)
    },
    clearAccountToken (state) {
      localStorage.clear('accountToken')
    }
  },
  actions: {
    // 登录
    login ({commit}, form) {
      return new Promise((resolve, reject) => {
        axios({
          url: '/loginAndRegister/mobileLogin',
          method: 'post',
          data: {
            mobile: form.phone,
            password: form.password,
            clientId: form.clientId,
            imgCode: form.imgCode
          },
          transformRequest: [function (data) {
            // Do whatever you want to transform the data
            let ret = ''
            for (let it in data) {
              ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
            }
            return ret
          }],
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
          }
        }).then(function (response) {
          if (response.data.success === true && response.data.code === '1') {
            commit('setAccountToken', response.data.data.token)
          }
          resolve(response)
        }).catch(function (response) {
          reject(response)
        })
      })
    },
    // 获取手机短信验证码
    getSmsCode ({commit}, form) {
      return new Promise((resolve, reject) => {
        axios({
          url: '/loginAndRegister/getSmsCode',
          method: 'post',
          data: {
            mobile: form.phone,
            type: form.type,
            imageCode: form.imageCode
          },
          transformRequest: [function (data) {
            // Do whatever you want to transform the data
            let ret = ''
            for (let it in data) {
              ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
            }
            return ret
          }],
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
          }
        }).then(function (response) {
          resolve(response)
        }).catch(function (response) {
          reject(response)
        })
      })
    },
    // 注册
    regist ({commit}, form) {
      return new Promise((resolve, reject) => {
        axios({
          url: '/loginAndRegister/register',
          method: 'post',
          data: {
            mobile: form.phone,
            password: form.password,
            code: form.code,
            resource: form.resource
          },
          transformRequest: [function (data) {
            // Do whatever you want to transform the data
            let ret = ''
            for (let it in data) {
              ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
            }
            return ret
          }],
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
          }
        }).then(function (response) {
          resolve(response)
        }).catch(function (response) {
          reject(response)
        })
      })
    }
  }
}



