import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '*',
      redirect: '/home'
    },
    {
      path: '/login',
      name: '登录',
      component: resolve => require(['@/views/login/LoginView.vue'], resolve),
      meta: {
        keepAlive: true
      }
    },
    {
      path: '/regist',
      name: '注册',
      component: resolve => require(['@/views/login/RegistView.vue'], resolve),
      meta: {
        keepAlive: true
      }
    },
    {
      path: '/home',
      name: '首页',
      component: resolve => require(['@/views/home/HomeView.vue'], resolve),
      meta: {
        keepAlive: true,
        requireAuth: true
      }
    },
    {
      path: '/data/more',
      name: '更多数据',
      component: resolve => require(['@/views/home/MoreDataView.vue'], resolve),
      meta: {
        keepAlive: true
      }
    }
  ]
})



